<script>
export default {
  mounted() {
    const mapBtns = Array.from(document.querySelectorAll(".map-button"));

    console.log(mapBtns);

    mapBtns.forEach((btn) => {
      const num = btn.getAttribute("data-map-btn");
      const currentItem = document.querySelector(
        `.map-item[data-map-item="${num}"]`
      );
      if (currentItem) {
        const currentItemClose = document.querySelector(
          `.map-item[data-map-item="${num}"] .map-item__close`
        );

        btn.addEventListener("click", (e) => {
          if (currentItem.classList.contains("active")) {
            currentItem.classList.remove("active");
            currentItem.style.top = `${0}px`;
            currentItem.style.left = `${0}px`;
          } else {
            currentItem.classList.add("active");

            if (e.target.offsetTop < currentItem.clientHeight) {
              currentItem.style.left = `${e.target.offsetLeft + 25}px`;
              currentItem.style.top = `${e.target.offsetTop + 25}px`;
              console.log(currentItem.clientHeight);
            } else {
              currentItem.style.left = `${e.target.offsetLeft + 25}px`;
              currentItem.style.top = `${e.target.offsetTop - currentItem.clientHeight}px`;
            }
          }
        });
        currentItemClose.addEventListener("click", () => {
          currentItem.classList.remove("active");
          currentItem.style.top = `${0}px`;
          currentItem.style.left = `${0}px`;
        });
      }
    });
  },
};
</script>

